.login body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
}

.main5 {
  margin: 10rem 0;
}

.login {

  background-color: #fff;

  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  width: 600px;
  padding: 50px;
}

.login .form {
  width: 100%;
}

.login h2 {
  font-size: 20px;
  text-align: left;
  padding: 3rem 0;
  font-weight: 700;
}

.login .input-container {
  margin-bottom: 15px;
  width: 95%;
}

.login label {
  display: block;
  margin-bottom: 5px;
}

.login input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 3px;
}

.login button {
  width: 100%;
  padding: 10px;
  background-color: #007BFF;
  color: #fff;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.login button:hover {
  background-color: #0056b3;
}